body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
    padding: 0;
    overflow: hidden;
}

/******* Top Nav **********/

#topbar {
    color: #F9F9F9;
    display: flex;
    width: 100%;
}

/******* Side Menu *******/

#navMenu {
    opacity: 0.9;
    display: flex;
    color: #F9F9F9;
    flex-flow: column;
    max-width: 250px;
    float: left;
    margin: 0;
    height: 100vh;
    overflow: scroll;
}

#navbar-span {
    margin: 0 10px 0 10px;
    padding: 20px 0 20px 0;
}

ul > li {
    padding: 15px 0 15px 0;
    cursor: pointer;
    border: 1px #F9F9F9 solid;
    border-radius: 15px;
}


/******** MAP ***********/

#fourInfo {
    font-size: 8px;
    font-weight: 600;
}

#fourTitle {
    color: #E83766;
}

#errorMess {
    padding-top: 150px;
    color: #000;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

